import getClipInfo from '../request/getClipInfo';
import swiper, { makeColumn } from '../slider&swiper';

const key = 'AIzaSyA8pD_BQR5urHLwDmjvDZn_v0iQphBz3zQ';

function drowItems(clipsInfo) {
  const slider = document.createElement('div');
  slider.classList.add('slider');
  const row = document.createElement('div');
  row.classList.add('row');

  const points = document.createElement('div');
  points.classList.add('points');
  points.setAttribute('data', 'points');

  const pointPrev = document.createElement('button');
  pointPrev.classList.add('points__point');
  pointPrev.setAttribute('data', 'pointPrev');

  const pointMiddle = document.createElement('button');
  pointMiddle.classList.add('points__point');
  pointMiddle.setAttribute('data', 'pointMiddle');

  const pointNext = document.createElement('button');
  pointNext.classList.add('points__point');
  pointNext.setAttribute('data', 'pointNext');

  clipsInfo.forEach(async (item) => {
    row.appendChild(await makeColumn(item));
  });

  points.appendChild(pointPrev);
  points.appendChild(pointMiddle);
  points.appendChild(pointNext);
  slider.appendChild(row);
  slider.appendChild(points);
  document.body.appendChild(slider);
}

export default function search() {
  const searchInput = document.querySelector('[data="searchInput"]');
  searchInput.addEventListener('change', async () => {
    const slider = document.querySelector('.slider');
    if (slider) document.body.removeChild(slider);
    const url1 = `https://www.googleapis.com/youtube/v3/search?key=${key}`;
    const url = `${url1}&type=video&part=snippet&maxResults=15&q=${searchInput.value}`;
    const clipsInfo = await getClipInfo(url);
    drowItems(clipsInfo.items);
    swiper(url, clipsInfo.nextPageToken);
  });
}
