import './slider&swiper-style.css';

const key = 'AIzaSyA8pD_BQR5urHLwDmjvDZn_v0iQphBz3zQ';

export async function makeColumn(item) {
  const {
    snippet: {
      title,
      channelTitle,
      thumbnails,
      description,
      publishedAt,
    },
    id: {
      videoId,
    },
  } = item;

  const url2 = `https://www.googleapis.com/youtube/v3/videos?key=${key}&id=${videoId}&part=statistics`;
  const responce2 = await fetch(url2);
  const statistics = await responce2.json();
  const viewInfo = statistics.items[0].statistics.viewCount;

  const img = document.createElement('img');
  img.setAttribute('width', thumbnails.medium.width);
  img.setAttribute('height', thumbnails.medium.height);
  img.setAttribute('src', thumbnails.medium.url);

  const titleVideo = document.createElement('h4');

  const linkToVideo = document.createElement('a');
  linkToVideo.setAttribute('href', `https://www.youtube.com/watch?v=${videoId}`);
  linkToVideo.innerHTML = title;

  const view = document.createElement('li');
  view.innerHTML = `<i class="fas fa-eye"></i>Views: ${viewInfo}`;

  const descriptionVideo = document.createElement('p');
  descriptionVideo.classList.add('description');
  descriptionVideo.innerHTML = description;

  const channel = document.createElement('li');
  channel.innerHTML = `<i class="fas fa-male"></i>${channelTitle}`;

  const dateAt = document.createElement('li');
  const date = new Date(publishedAt);
  dateAt.innerHTML = `<i class="fas fa-calendar-week"></i>${date.getFullYear()}/${date.getMonth()}/${date.getDate()}`;

  const ul = document.createElement('ul');

  const div = document.createElement('div');
  div.classList.add('item');

  titleVideo.appendChild(linkToVideo);

  ul.appendChild(view);
  ul.appendChild(dateAt);
  ul.appendChild(channel);

  div.appendChild(img);
  div.appendChild(titleVideo);
  div.appendChild(ul);
  div.appendChild(descriptionVideo);
  return div;
}

export default function swiper(url, next) {
  const slider = document.querySelector('.row');
  let isDown = false;
  let startX;
  let scrollLeft;
  let slideNum = 1;
  let nextKey = next;
  let requestNum = 1;
  let walk;
  const pointPrev = document.querySelector('[data="pointPrev"]');
  const pointMiddle = document.querySelector('[data="pointMiddle"]');
  pointMiddle.innerHTML = slideNum;
  const pointNext = document.querySelector('[data="pointNext"]');

  async function requestNext() {
    const urlNext = `${url}&pageToken=${nextKey}`;
    requestNum += 1;
    const responce = await fetch(urlNext);
    const data = await responce.json();
    const { items } = data;
    nextKey = data.nextPageToken;
    const { documentElement: { clientWidth } } = document;
    const row = document.getElementsByClassName('row')[0];

    if (clientWidth > 700) {
      row.style.gridTemplateColumns = `repeat(${15 * requestNum}, 24%`;
    } else if (clientWidth > 500) {
      row.style.gridTemplateColumns = `repeat(${15 * requestNum}, 49%`;
    } else {
      row.style.gridTemplateColumns = `repeat(${15 * requestNum}, 99%`;
    }

    items.forEach(async (item) => {
      row.appendChild(await makeColumn(item));
    });
  }

  slider.addEventListener('mousedown', (e) => {
    isDown = true;
    slider.classList.add('active');
    startX = e.pageX - slider.offsetLeft;
    ({ scrollLeft } = slider);
  });

  slider.addEventListener('mouseleave', () => {
    isDown = false;
    slider.classList.remove('active');
  });

  slider.addEventListener('mouseup', () => {
    isDown = false;
    slider.classList.remove('active');

    const { documentElement: { clientWidth } } = document;
    if (walk < 0) {
      slideNum += 1;
      slider.scrollLeft = clientWidth * (slideNum - 1);
      pointMiddle.innerHTML = slideNum;
      if (slideNum > 2 && slideNum % 3 === 0) requestNext();
    } else {
      if (slideNum > 1) slideNum -= 1;
      slider.scrollLeft = clientWidth * (slideNum - 1);
      pointMiddle.innerHTML = slideNum;
    }
  });

  slider.addEventListener('mousemove', (e) => {
    if (!isDown) return false;
    e.preventDefault();
    const x = e.pageX - slider.offsetLeft;
    walk = x - startX;
    slider.scrollLeft = scrollLeft - walk;
    return true;
  });

  // touch
  slider.addEventListener('touchstart', (e) => {
    isDown = true;
    slider.classList.add('active');
    startX = e.changedTouches[0].pageX - slider.offsetLeft;
    ({ scrollLeft } = slider);
  });

  slider.addEventListener('touchleave', () => {
    isDown = false;
    slider.classList.remove('active');
  });

  slider.addEventListener('touchend', () => {
    isDown = false;
    slider.classList.remove('active');

    const { documentElement: { clientWidth } } = document;
    if (walk < 0) {
      slideNum += 1;
      slider.scrollLeft = clientWidth * (slideNum - 1);
      pointMiddle.innerHTML = slideNum;
      if (slideNum > 2 && slideNum % 3 === 0) requestNext();
    } else {
      if (slideNum > 1) slideNum -= 1;
      slider.scrollLeft = clientWidth * (slideNum - 1);
      pointMiddle.innerHTML = slideNum;
    }
  });

  slider.addEventListener('touchmove', (e) => {
    if (!isDown) return false;
    e.preventDefault();
    const x = e.changedTouches[0].pageX - slider.offsetLeft;
    walk = x - startX;
    slider.scrollLeft = scrollLeft - walk;
    return true;
  });

  pointPrev.addEventListener('click', () => {
    const { documentElement: { clientWidth } } = document;
    if (slideNum > 1) slideNum -= 1;
    slider.scrollLeft = clientWidth * (slideNum - 1);
    pointMiddle.innerHTML = slideNum;
  });

  pointPrev.addEventListener('mousedown', () => {
    if (slideNum === 1) return false;
    pointPrev.innerHTML = slideNum - 1;
    return true;
  });

  pointPrev.addEventListener('mouseup', () => {
    pointPrev.innerHTML = '';
  });

  pointNext.addEventListener('click', () => {
    const { documentElement: { clientWidth } } = document;
    slideNum += 1;
    slider.scrollLeft = clientWidth * (slideNum - 1);
    pointMiddle.innerHTML = slideNum;
    if (slideNum % 3 === 0) requestNext();
  });

  pointNext.addEventListener('mousedown', () => {
    pointNext.innerHTML = slideNum + 1;
  });

  pointNext.addEventListener('mouseup', () => {
    pointNext.innerHTML = '';
  });

  function mobileFun(m) {
    const row = document.getElementsByClassName('row')[0];

    if (m.matches) {
      row.style.gridTemplateColumns = `repeat(${15 * requestNum}, 99%`;
    } else {
      row.style.gridTemplateColumns = `repeat(${15 * requestNum}, 49%`;
    }
  }

  function tableFun(t) {
    const row = document.getElementsByClassName('row')[0];

    if (t.matches) {
      row.style.gridTemplateColumns = `repeat(${15 * requestNum}, 49%`;
    } else {
      row.style.gridTemplateColumns = `repeat(${15 * requestNum}, 24%`;
    }
  }

  const table = window.matchMedia('(max-width: 700px)');
  table.addListener(tableFun);

  const mobile = window.matchMedia('(max-width: 500px)');
  mobile.addListener(mobileFun);

  const addEvent = (object, type, callback) => {
    if (object === null || typeof (object) === 'undefined') return;
    if (object.addEventListener) {
      object.addEventListener(type, callback, false);
    } else if (object.attachEvent) {
      object.attachEvent(`on${type}`, callback);
    } else {
      const o = object;
      o[`on${type}`] = callback;
    }
  };

  addEvent(window, 'resize', () => {
    const { documentElement: { clientWidth } } = document;
    slider.scrollLeft = clientWidth * (slideNum - 1);
  });
}
