export default function searchView() {
  // add meta
  const meta1 = document.createElement('meta');
  meta1.content = 'width=device-width, initial-scale=1';
  meta1.name = 'viewport';
  document.getElementsByTagName('head')[0].appendChild(meta1);

  const meta2 = document.createElement('meta');
  meta2.httpEquiv = 'X-UA-Compatible';
  meta2.content = 'IE=edge';
  document.getElementsByTagName('head')[0].appendChild(meta2);

  document.head.innerHTML += '<link rel="stylesheet" href="https://use.fontawesome.com/releases/v5.8.2/css/all.css" integrity="sha384-oS3vJWv+0UjzBfQzYUhtDYW+Pj2yciDJxpsK1OYPAYjqT085Qq/1cq5FLXAZQ7Ay" crossorigin="anonymous">';
  document.head.innerHTML += '';
  // add search
  const search = document.createElement('input');
  search.classList += 'search';
  search.setAttribute('data', 'searchInput');
  search.setAttribute('placeholder', 'Search...');
  document.body.appendChild(search);
}
